<template lang="pug">
.asset-form
  .box
    .box-header
      | 
    .box-content
      el-form(:model="demo", :rules="rules", ref="assetForm", label-width="120px")
        el-row
          el-col(:span="12")
            el-form-item(label="", prop="type")
              el-input(type="text", placeholder="", v-model="demo.name")
            el-form-item(label="", prop="amount")
              el-input(type="text", placeholder="", v-model="demo.amount")
                template(slot="append") 
            el-form-item(label="", prop="ratio")
              el-input(type="text", placeholder="", v-model="demo.ratio")
                template(slot="append") %
            el-form-item(label="", prop="startDate")
              el-date-picker(type="date", :value="demo.startDate", @input="handleStartDate", placeholder="")
            el-form-item(label="", prop="term")
              el-input(type="text", placeholder="", v-model="demo.term")
                template(slot="append") 
            el-form-item(label="", prop="payFrequency")
              el-input(type="text", placeholder="", v-model="demo.payFrequency")
            el-form-item(label="", prop="assetType")
              el-input(type="text", placeholder="", v-model="demo.assetType")
            el-form-item(label="", prop="publisher")
              el-input(type="text", placeholder="", v-model="demo.publisher")
            el-form-item(label="", prop="trustee")
              el-input(type="text", placeholder="", v-model="demo.trustee")
          el-col(:span="12")
            el-form-item(label="", prop="shortName")
              el-input(type="text", placeholder="", v-model="demo.shortName")
            el-form-item(label="", prop="issueAmount")
              el-input(type="text", placeholder="", v-model="demo.issueAmount")
                template(slot="append") 
            el-form-item(label="", prop="rate")
              el-input(type="text", placeholder="", v-model="demo.rate")
                template(slot="append") %
            el-form-item(label="", prop="endDate")
              el-date-picker(type="date", :value="demo.endDate", @input="handleEndDate", placeholder="")
            el-form-item(label="", prop="riskLevel")
              el-select(v-model="demo.riskLevel")
                el-option(v-for="o in riskLevels", :label="o.name", :value="o.value")
            el-form-item(label="", prop="payDate")
              el-date-picker(type="date",  placeholder="", :value="demo.payDate", @input="handlePayDate",)
            el-form-item(label="", prop="credit")
              el-input(type="text", placeholder="", v-model="demo.credit")
            el-form-item(label="", prop="gurantee")
              el-input(type="text", placeholder="", v-model="demo.gurantee")
            el-form-item(label="", prop="flow")
              el-select(v-model="demo.flow")
                el-option(v-for="o in flows", :label="o.name", :value="o.value")
  account-card(:passed-accounts='demo.accounts')
  .bottom-buttons
    el-button(type="primary", size="small", @click="submitForm") 
    el-button(type="gray", size="small", @click="cancel") 

</template>

<script>
import {
  MessageBox,
  Message
} from 'element-ui'
import {
  updateCrumbs
} from '@/common/crossers.js'
import AccountCard from '@/components/AccountCard.vue'
import moment from 'moment'
import {
  uniqueId
} from 'lodash'

export default {
  components: {
    AccountCard
  },
  mounted() {
    if (this.$route.params.id !== 'add') {
      const name = this.$route.params.id
      updateCrumbs.$emit('update-crumbs', [{
        id: 'assetName',
        name: name
      }])
      this.demo = {
        name: name,
        amount: 33816000,
        ratio: 16.5,
        startDate: '2017-01-04',
        term: 90,
        payFrequency: '',
        assetType: '',
        publisher: '',
        trustee: '',
        shortName: name,
        issueAmount: 200897170,
        rate: 8,
        endDate: '2017-03-03',
        riskLevel: 'L',
        payDate: '2017-01-08',
        credit: '',
        gurantee: name,
        flow: 'N',
        accounts: [{
          id: uniqueId(),
          checked: true,
          type: '',
          belongto: name,
          name: name,
          bankNum: '2310 8372 6532 098',
          bank: ''
        }]
      }
    }
  },

  methods: {
    submitForm() {
      Message.info('')
    },
    cancel() {
      MessageBox.confirm('', '', {
        type: 'warning'
      }).then(() => {
        this.$router.back()
      })
    },
    handleStartDate(value) {
      if (!value) return
      this.demo.startDate = moment(value).format('YYYY-MM-DD')
    },
    handleEndDate(value) {
      if (!value) return
      this.demo.endDate = moment(value).format('YYYY-MM-DD')
    },
    handlePayDate(value) {
      if (!value) return
      this.demo.payDate = moment(value).format('YYYY-MM-DD')
    }
  },

  data() {
    return {
      rules: {},
      demo: {
        name: null,
        amount: null,
        ratio: null,
        startDate: null,
        term: null,
        payFrequency: null,
        assetType: null,
        publisher: null,
        trustee: null,
        shortName: null,
        issueAmount: null,
        rate: null,
        endDate: null,
        riskLevel: 'L',
        payDate: null,
        credit: null,
        gurantee: null,
        flow: 'N',
        accounts: []
      },
      pickOpt: {
        format: 'yyyy-MM-dd'
      },
      riskLevels: [{
        name: '',
        value: 'L'
      }, {
        name: '',
        value: 'M'
      }, {
        name: '',
        value: 'H'
      }],
      flows: [{
        name: '',
        value: 'N'
      }, {
        name: '',
        value: 'H'
      }, {
        name: '',
        value: 'L'
      }]
    }
  }
}
</script>

<style lang="scss">
.accounts-list {
  .box {
    border: 0;
    padding: 10px 0;
    .box-tab-header {
      top: 12px;
      right: 0;
    }
    .filters {
      padding: 0;
    }
  }
}
</style>



